$gutenslider-primary: #8CA6DB;
$gutenslider-dark: #9D50BB;
$gutenslider-light: #B993D6;

@mixin gs-background{
	background: #B993D6;  /* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #8CA6DB, #B993D6);  /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #8CA6DB, #B993D6); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

//link color in plugin list
tr[data-plugin="eedee-gutenslider/eedee-gutenslider.php"] {
	.upgrade a {
		color: #46B450;
	}
}

.dashicons,
.fs-site-icon,
.fs-plugin-icon,
.dashicons-plus::before {
	box-sizing: content-box;
	-webkit-box-sizing: content-box;
}

