.gutenslider-admin {
	padding-top: 12px;

	a {
		color: #9D50BB;
	}

	img {
		width: 100%;
		height: auto;

		&.gutenslider-icon {
			max-width: 200px;
		}
	}

	#wpcontent {
		padding: 0;
		font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
	}

	.gutenslider-admin {
		margin: 0;
	}

	section.pricing {
		letter-spacing: 2px;
	}

	section.heading {
		// @include gs-background;
		letter-spacing: 1px;
		border-bottom: 1px solid #c3c4c7;

		h2 {
			// color: #fff;
			padding: 0;
		}

		.gs-admin-intro {
			height: 200px;
			// color: #fff;

			> div > div {
				width: 100%;
			}

			a {
				color: $gutenslider-dark;
			}
		}
	}

	h2 {
		font-size: 40px;
	}

	h3 {
		font-size: 35px;
	}

	h4 {
		color: #000;
	}

	section p {
		font-size: 18px;
	}

	section.blog-posts {
		// letter-spacing: 1.5px;

		hr {
			width: 100%;
			margin: 1.5rem 0;
		}

		.link-to-blog {
			width: 100%;
			text-align: center;
		}

		.post-read-more {
			@include gs-background;

			font-size: 80%;
		  letter-spacing: .1rem;
		  font-weight: bold;
		  padding: 1rem;
		  transition: all 0.2s;
			color: #333;

			&:hover {
				color: #111;
			}
		}
	}

	section.pricing {
		@include gs-background;

		h3 {
			width: 100%;
			text-align: center;
			margin-bottom: 3rem;
			margin-top: 1rem;

			small {
				display: block;
				font-size: 1.2rem;
			}
		}

		ul {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			text-align: center;
		}

		.card,
		.row,
		.card-body {
			height: 100%;
		}

		.card {
			max-width: 100%;
			margin-bottom: 2rem;
		  border: none;
		  border-radius: 1rem;
		  transition: all 0.5s;
		  box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.1);

			.btn {
				background: $gutenslider-primary;
			}
		}

		hr {
		  margin: 1.5rem 0;
		}

		.card-title {
		  margin: 0.5rem 0;
		  font-size: 0.9rem;
		  letter-spacing: .1rem;
		  font-weight: bold;
		}

		.card-price {
		  font-size: 3rem;
		  margin: 0;
		}

		.card-price .period {
		  font-size: 0.8rem;
		}

		ul li {
		  margin-bottom: 1rem;
		}

		.card-body {
			display: flex;
			flex-direction: column;
		}

		.card-body ul {
			flex: 1;
		}

		.text-muted {
		  opacity: 0.7;
		}

		.btn {
		  font-size: 80%;
		  border-radius: 5rem;
		  letter-spacing: .1rem;
		  font-weight: bold;
		  padding: 1rem;
		  opacity: 0.7;
		  transition: all 0.2s;
			color: #555;

			&:hover {
				color: #000;
			}
		}

		/* Hover Effects on Card */

		@media (min-width: 992px) {
		  .card:hover {
		    transform: translate3d(0,-15px,0);
		    box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.3);
		  }
		  .card:hover .btn {
		    opacity: 1;
		  }
		}
	}

	section.thankyou {
		// @include gs-background;
		border-top: 1px solid #c3c4c7;

		p {
			margin-bottom: 0;
		}
	}
}

